export default {
  props: ['setting'],
  data: function data() {
    return {
      email: ''
    };
  },
  mounted: function mounted() {},
  methods: {
    gotoUrl: function gotoUrl(url) {
      if (this.$store.state.userInfo) {
        window.location.href = url;
      } else {
        if (url != 'home' && url != 'products-market') {
          if (url.indexOf("http") != -1) {
            window.location.href = url;
          } else {
            this.openDialogLogin();
          }
        } else {
          window.location.href = url;
        }
      }
    },
    goto: function goto(name) {
      if (this.$store.state.userInfo) {
        this.$router.push({
          name: name
        });
      } else {
        if (name != 'home' && name != 'productsMarketHome') {
          this.openDialogLogin();
        } else {
          this.$router.push({
            name: name
          });
        }
      }
    },
    openDialogLogin: function openDialogLogin() {
      this.$root.$children[0].openDialogLogin();
    },
    openRegister: function openRegister() {
      this.$root.$children[0].openDialogRegister();
    },
    submitSubscribe: function submitSubscribe() {
      var _this = this;

      if (!this.email) {
        return;
      }

      this.$showLoading();
      this.$apiCall("api.Market.subscribe", {
        email: this.email
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this.email = "";

          _this.$message.success("Email Submitted Successfully !");
        } else {
          _this.$message.error(r.Message);
        }

        _this.$hideLoading();
      });
    }
  }
};