import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import DHeader from '@/views/homePage/components/header';
import Footer from '@/views/homePage/components/footer';
export default {
  data: function data() {
    return {
      headerMenu: [],
      setting: {
        header: {}
      },
      loading: false,
      id: this.$route.params.id,
      page: {
        content: null,
        id: null,
        title: null,
        updateTime: null
      },
      code: {
        header: [],
        footer: []
      }
    };
  },
  components: {
    DHeader: DHeader,
    Footer: Footer
  },
  watch: {
    '$route.params.id': {
      handler: function handler() {
        this.getInfo();
      },
      deep: true
    }
  },
  created: function created() {
    this.getInfo();
  },
  mounted: function mounted() {},
  methods: {
    getInfo: function getInfo() {
      var _this = this;

      // location.host.split('.').length < 3 || 
      if (location.host.split('.')[0].toLowerCase() == 'www' || location.host.split('.')[0].toLowerCase() == 'sandboxwww' || location.host.split('.')[0].toLowerCase() == 'supplier' || location.host.split('.')[0].toLowerCase() == 'sandboxsupplier' && location.host.indexOf('localhost') < 0) {
        return;
      }

      this.loading = true; // location.host

      var url = location.host;

      if (location.host.indexOf('localhost') > -1) {
        //测试
        url = 'shop242.myourmall.com';
      }

      this.$apiCall('api.User.getInfoFromUrl', {
        url: url
      }, function (r) {
        if (r.ErrorCode == 9999) {
          localStorage.setItem('c_apiShopId', r.Data.Results.shopId);

          _this.getItem();
        } else {
          _this.loading = false;

          _this.$elementMessage(r.Message, "error");
        }
      });
    },
    getItem: function getItem() {
      var _this2 = this;

      this.$apiCall("api.VendorShop.getShopConfig", {}, function (r) {
        _this2.loading = false;

        if (r.ErrorCode == 9999) {
          if (r.Data.Results.length !== 0) {
            if (r.Data.Results.header.activeMenu) {
              //有启用头部菜单
              r.Data.Results.menu.forEach(function (item) {
                if (item.id == r.Data.Results.header.activeMenu) {
                  _this2.headerMenu = item;
                }
              });
            }

            _this2.$store.commit('getStoreComponent', r.Data.Results);

            _this2.setting = r.Data.Results;

            if (_this2.$route.params.id) {
              _this2.id = _this2.$route.params.id;
            }

            if (_this2.setting.customPage) {
              _this2.setting.customPage.forEach(function (item) {
                if (_this2.id == item.id) {
                  _this2.page = item;
                }
              });
            }

            localStorage.setItem("c_shopConfigJson", JSON.stringify(r.Data.Results));
          } else {
            _this2.setting = JSON.parse(JSON.stringify(_this2.$store.state.configJson));
          }
        } else {
          _this2.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    }
  }
};